import { css, html, LitElement } from "lit";
import { customElement, property } from "lit/decorators.js";

type PageType = number | "...";

@customElement("lit-paginator")
export class LitPaginator extends LitElement {
  @property({ type: Number }) total = 0;
  @property({ type: Number }) currentPage = 1;

  static styles = css`
    :host {
      display: flex;
      align-items: center;
      gap: 10px;
      user-select: none;
    }
    .page-button {
      cursor: pointer;
      padding: 5px 10px;
      border: 1px solid #ccc;
      border-radius: 4px;
    }
    .page-button:hover {
      background-color: #f0f0f0;
    }
    .page-button.active {
      background-color: #007bff;
      color: white;
    }
    .page-button:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  `;

  render() {
    const pages = this.generatePageNumbers();

    return html`
      </button>

      ${pages.map((page) =>
        page === "..."
          ? html`<span>...</span>`
          : html`
              <button
                class="page-button ${page === this.currentPage ? "active" : ""}"
                @click=${() => this.changePage(page)}
              >
                ${page}
              </button>
            `
      )}
    `;
  }

  generatePageNumbers(): PageType[] {
    const offset = 3;
    const currentPage = this.currentPage;
    const total = this.total;

    if (this.total <= 6) {
      return Array.from({ length: this.total })
        .fill(0)
        .map((_, i) => i);
    }

    if (currentPage + offset >= total) {
      return [1, "...", total - 2, total - 1, total];
    }

    if (currentPage - offset <= 0) {
      return [1, 2, 3, "...", total];
    }

    return [
      1,
      "...",
      currentPage - 1,
      currentPage,
      currentPage + 1,
      "...",
      total,
    ];
  }

  changePage(page: number): void {
    if (page < 1 || page > this.totalPages) return;
    this.currentPage = page;
    this.dispatchEvent(
      new CustomEvent("page-change", {
        detail: { page },
      })
    );
  }
}
